<template>
  <v-app>
    <v-main>
      <v-container class="fill" fluid>
        <v-row class="text-center">
          <v-col cols="12">
            <v-img :src="require('../assets/45985.jpg')" class="my-3" contain height="200"/>
          </v-col>
        </v-row>
        <v-row align="center" justify="center">
          <v-col cols="12" sm="8" md="4">
            <center><h1>meetAPet - Adote um amigo!</h1></center>
          </v-col>
        </v-row>
        <v-row align="center" justify="center">
          <v-col cols="12" sm="8" md="4">
            <v-card class="elevation-12">
              <v-card-text>
                <v-form>
                  <v-text-field label="Email" name="email" prepend-icon="mdi-account" type="text" v-model="email"></v-text-field>
                  <v-text-field id="password" label="Password" name="senha" prepend-icon="mdi-lock" type="password" v-model="senha"></v-text-field>
                </v-form>
              </v-card-text>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="primary" @click="login">Login</v-btn>
                <v-btn color="primary" @click="registrar">Criar uma conta</v-btn>
              </v-card-actions>
            </v-card>
          </v-col>
        </v-row>
      </v-container>
    </v-main>
  </v-app>
</template>

<script>
import firebase from 'firebase'

export default {
  name: 'sign-up',
  data () {
    return {
      email: '',
      senha: ''
    }
  },
  methods: {
    login: function () {
      firebase.auth().signInWithEmailAndPassword(this.email, this.senha)
        .then((user) => {
          this.$router.push('/home').catch(error => {
            if (error.name !== 'NavigationDuplicated') {
              throw error
            }
          })
          alert(`Bem vindo ${this.email}`)
        })
        .catch((err) => {
          alert(`Não foi possível realizar o login: ${err.message}`)
        })
    },
    registrar: function () {
      this.$router.push('registrar').catch(error => {
        if (error.name !== 'NavigationDuplicated') {
          console.log('akiii')
          throw error
        }
      })
      // this.$router.replace('registrar')
    }
  }
}
</script>
